.gig-date {
  display: none; }

/* EUGH */
.hide-gig {
  display: none !important; }

/* Users */
.user {
  font-family: 'Gafata';
  font-size: 3em;
  color: #000; }

/* Modules */
.popup {
  position: fixed;
  box-shadow: 0 0 10px #000;
  z-index: 3;
  display: none;
  width: 450px;
  background: url(/images/bg_main.png);
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%); }
  .popup h3 {
    margin: 0;
    border-bottom: 1px solid #ccc;
    color: #777;
    border-top: 3px solid #E62534;
    background: #fff;
    padding: 15px; }
  .popup form {
    padding: 15px; }

.popup-shadow {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #000;
  opacity: 0.4;
  z-index: 2;
  top: 0;
  left: 0;
  display: none; }

.notification-popup {
  width: 60%; }
  .notification-popup .notification {
    margin: 0 auto; }

.notification-bubble {
  position: absolute;
  background: #333;
  color: #fff;
  padding: 10px;
  border-radius: 21px;
  box-shadow: 0px 0px 6px #000; }
  .notification-bubble:after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border-top: 9px solid transparent;
    border-bottom: 7px solid transparent;
    border-left: 9px solid #333;
    position: absolute;
    right: -7px;
    top: 12px; }
  .notification-bubble a {
    color: #fff; }

.notification.widget {
  padding: 0; }

#user-notifications {
  right: 470px;
  top: 14px; }

/* MODULES */
/* Top artists */
.top-artists {
